'use strict';
class Vehichle {
    constructor(name, wheels) {
        this.name = name;
        this. wheels = wheels;
    };
};

class Car extends Vehichle {
    constructor(name){
        super(name, 4);
    }
};

class Motorcycle extends Vehichle {
    constructor(name){
        super(name, 2);
    }
}
module.exports = {Vehichle};
module.exports = {Car};
module.exports = {Motorcycle};
